.ResultsWrapper {
  width: 100%;
  max-height: 100vh;

  .resultsContent {

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    justify-content: center;

    padding-bottom: 2rem;

    height: calc(100vh - 100px);
    overflow: scroll;
  }

}



